@import url(https://fonts.googleapis.com/css?family=Roboto:100,400,700&display=swap);

    * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

body{

    font-family: 'RobotoMono-Thin';

    /* center screen */
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    text-align: center;

    color: white;

    /* background */
    background-color: #1b2f4e;
    background-image: url("https://www.transparenttextures.com/patterns/green-dust-and-scratches.png");
    /* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */

}

.calculator-body{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.calculator {

    color: white;
    height: 320px;
    width: 235px;
    border-radius: 5px;
    overflow: hidden;

    /* buttons */
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: 1fr repeat(5, 48px);

}
:root{
    --bg-button: #f0f0f0f0;
    --border-button: solid 1px #888;

}

@font-face{
    src: url(/react-calculator/static/media/RobotoMono-Bold.7c13b043.ttf);
    font-family: 'RobotoMono-Bold';
    /* src: url('../fonts/RobotoMono-Thin.ttf');
    font-family: 'RobotoMono'; */
}

.button{
    font-family: 'RobotoMono-Bold';
    font-size: 1.4em;
    background-color: #f0f0f0f0;
    background-color: var(--bg-button);
    border: none;
    border-right: solid 1px #888;
    border-right: var(--border-button);
    border-bottom: solid 1px #888;
    border-bottom: var(--border-button);
    padding-left: 10px;
    padding-right: 10px;
    outline: none;

    cursor: pointer;
}

.button:active{
    background-color: #ccc;
}

.button.double{
    grid-column: span 2;
}

.button.triple {
    grid-column: span 3;
}

.button.operation{
    background-color: #fa8231;
    color: white;
}

.button.operation:active{
    background-color: #fa823150;
}




.display{
    font-family: 'RobotoMono-Thin';
    grid-column: span 4;
    
    background-color: #0008;

    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 0 5px 5px 0;
    font-size: 35px;
}
.footer{
    padding: 10px;
}

.footer a{
    text-decoration: none;
    color:white;
    font-weight: bold;
    margin: 0 5px;
}

.icon{
    color: red;
}

.footer .social{
    margin-top: 20px;
}
