
    @import url('https://fonts.googleapis.com/css?family=Roboto:100,400,700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

body{

    font-family: 'RobotoMono-Thin';

    /* center screen */
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    text-align: center;

    color: white;

    /* background */
    background-color: #1b2f4e;
    background-image: url("https://www.transparenttextures.com/patterns/green-dust-and-scratches.png");
    /* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */

}

.calculator-body{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.calculator {

    color: white;
    height: 320px;
    width: 235px;
    border-radius: 5px;
    overflow: hidden;

    /* buttons */
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: 1fr repeat(5, 48px);

}